import React, { Component } from "react";
import { Link } from "react-router-dom";
import { withAuth } from "./../lib/Auth";
import logo from "../img/logo.png";

class Signup extends Component {
  state = { username: "", password: "",email: '',  name:'', surname:'' };

  handleFormSubmit = event => {
    event.preventDefault();
    const { username, password , email,  name, surname } = this.state;

    this.props.signup(username, password, email,  name, surname);
  };

  handleChange = event => {
    const { name, value } = event.target;
    this.setState({ [name]: value });
  };

  render() {
    const { username, password, name, surname, email } = this.state;
    return (
      <div>

      <div className="signup-upper">
          <img src={logo} className="logo2" alt="logo"/>
        </div>
<div>
        <h1 className="section-header">SIGN UP</h1>
</div>
        <div className="login-form">
        <form onSubmit={this.handleFormSubmit}>
          <label>Username:</label>
          <input
            className="iyo"
            placeholder="Username"
            type="text"
            name="username"
            value={username}
            onChange={this.handleChange}
          />

           <label>Name:</label>
          <input
            type="text"
            name="name"
            value={name}
            onChange={this.handleChange}
          />

            <label>Surname:</label>
          <input
            type="text"
            name="surname"
            value={surname}
            onChange={this.handleChange}
          />

            <label>Email:</label>
          <input
            type="email"
            name="email"
            value={email}
            onChange={this.handleChange}
          />

          <label>Password:</label>
          <input
            type="password"
            name="password"
            value={password}
            onChange={this.handleChange}
          />
          <Link to="/dashboard" className="button-text">
            <input type="submit" className="login-button" value="SIGN UP" />
          </Link>
        </form>
      </div>



      <div className="signup-text">
          Already registered? <Link to="/login">Log in</Link>
      </div>

      </div>
    );
  }
}

export default withAuth(Signup);
